<template>
  <div class="container content">
    <div class="row">
      <div class="col-12">
        <h1 class="text-primary">Transaksi Transfer Bank</h1>
      </div>
    </div>
    <div class="row mt-3">
      <div class="col-12 mb-2" v-show="this.dataCheckNoTujuan.valid">
        <div
          class="card bg-putih shadow rounded-10"
          id="result-card"
          style="display: block"
        >
          <div class="card-body">
            <img
              v-if="this.dataCheckNoTujuan.operator == 'Indosat Ooredoo'"
              src="@/assets/operator/indosat-ooredoo.jpg"
              id="logo-operator"
            />
            <img
              v-if="this.dataCheckNoTujuan.operator == 'XL Axiata'"
              src="@/assets/operator/xl-axiata.jpg"
              id="logo-operator"
            />
            <img
              v-if="this.dataCheckNoTujuan.operator == '3'"
              src="@/assets/operator/3.jpg"
              id="logo-operator"
            />
            <img
              v-if="this.dataCheckNoTujuan.operator == 'Smartfren'"
              src="@/assets/operator/smartfren.jpg"
              id="logo-operator"
            />
            <img
              v-if="this.dataCheckNoTujuan.operator == 'Telkomsel'"
              src="@/assets/operator/telkomsel.jpg"
              id="logo-operator"
            />
          </div>
        </div>
      </div>
      <div v-show="this.noTujuanValid.status == false" class="col-12 mt-2">
        <div class="alert alert-danger rounded-10" role="alert">
          {{ this.noTujuanValid.message }}
        </div>
      </div>
      <div class="col-12">
        <div class="card bg-putih shadow rounded-10">
          <div class="card-body">
            <form class="text-left">
              <div class="form-group">
                <label for="jenis_bank">Jenis Bank</label>
                <select
                  @click="getDataJenisBank()"
                  v-model="idOperator"
                  id="jenis_bank"
                  name="jenis_bank"
                  required
                  class="form-control form-control-lg rounded-10"
                >
                  <option :value="null">Pilih</option>
                  <template
                    v-for="jenisBank in orderedDataJenisBank"
                    :key="jenisBank"
                  >
                    <option :value="jenisBank">
                      {{ jenisBank.product_name }} -
                      {{ jenisBank.product_id }}
                    </option>
                  </template>
                </select>
              </div>
              <div v-show="this.idOperator" class="form-group">
                <label for="no_tujuan" class="text-primary"
                  >No Rekening
                  <!-- <button
                    v-show="this.noTujuanValid.status == true"
                    class="btn btn-primary btn-sm"
                  >
                    Cek
                  </button> -->
                </label>
                <input
                  @input="checkRekening"
                  v-model="this.noTujuan"
                  id="no_tujuan"
                  required=""
                  autocomplete="off"
                  type="text"
                  class="
                    form-control form-control-lg
                    border border-primary
                    rounded-10
                  "
                  name="no_hp"
                  pattern="\d*"
                  placeholder="Masukkan Nomor Rekening.."
                />
              </div>
              <div
                v-show="this.noTujuanValid.status == true"
                class="form-group"
              >
                <label for="produk"
                  >Produk
                  <span
                    v-show="this.user.verified == '0'"
                    class="badge badge-danger"
                    >Saldo : Rp. {{ numberFormat(this.user.saldo) }}</span
                  ></label
                >
                <select
                  @click="loadProduct()"
                  v-model="idProduct"
                  id="produk"
                  name="produk"
                  required
                  class="form-control form-control-lg rounded-10"
                >
                  <option :value="null">Pilih</option>
                  <template
                    v-for="product in this.dataLoadProduct"
                    :key="product"
                  >
                    <option
                      :value="product.product_id"
                      v-if="product.product_name != 'Cek Rekening'"
                    >
                      {{ product.operator_name }}
                      {{ product.product_name }} Harga: Rp.
                      {{
                        numberFormat(
                          parseInt(
                            product.product_name.replace(/[^0-9]/g, "")
                          ) + 7000
                        )
                      }}
                    </option>
                  </template>
                </select>
              </div>
              <div
                v-if="this.user.verified == '1' && this.idProduct != null"
                class="form-group"
              >
                <label for="metode_pembayaran">Metode Pembayaran</label>
                <select
                  v-model="metodePembayaran"
                  id="metode_pembayaran"
                  name="metode_pembayaran"
                  required
                  class="form-control form-control-lg rounded-10"
                >
                  <option value="">Pilih</option>
                  <option :value="'Hutang'">Bayar Nanti</option>
                  <option :value="'Dompet'">
                    Dompet | Saldo Anda: Rp. {{ numberFormat(this.user.saldo) }}
                  </option>
                </select>
              </div>
              <div v-show="this.metodePembayaran">
                <button
                  @click="kirim()"
                  type="button"
                  class="btn btn-lg btn-block btn-primary rounded-10"
                >
                  Kirim
                </button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getOperator } from "no-telp";
import Swal from "sweetalert2";
import axios from "axios";
import _ from "lodash";

export default {
  name: "CusTransaksiTBView",
  props: ["ipAdress"],
  data() {
    return {
      user: JSON.parse(localStorage.getItem("SessionUser")),
      noTujuan: null,
      dataCheckNoTujuan: {},
      idOperator: null,
      idProduct: null,
      metodePembayaran: null,
      dataLoadProduct: null,
      dataJenisBank: null,
      noTujuanValid: {
        status: null,
        message: null,
      },
    };
  },
  methods: {
    getDataJenisBank() {
      axios({
        method: "get",
        url:
          this.ipAdress +
          "api/get-data-jenis-bank?username=" +
          this.user.username,
      })
        .then((response) => {
          //handle success
          this.dataJenisBank = response.data.filter(function (el) {
            return el.status == "ACTIVE";
          });
        })
        .catch((response) => {
          //handle error
          console.log(response);
        });
    },
    kirim() {
      if (!this.idOperator) {
        Swal.fire({
          icon: "error",
          title: "Jenis Bank Kosong",
          text: "silahkan pilih jenis bank!",
          showConfirmButton: false,
          timer: 1500,
        });
      } else if (!this.noTujuan) {
        Swal.fire({
          icon: "error",
          title: "No Tujuan",
          text: "silahkan masukkan no hp yang benar!",
          showConfirmButton: false,
          timer: 1500,
        });
      } else if (!this.idProduct) {
        Swal.fire({
          icon: "error",
          title: "Produk Kosong",
          text: "silahkan pilih produk!",
          showConfirmButton: false,
          timer: 1500,
        });
      } else if (!this.metodePembayaran) {
        Swal.fire({
          icon: "error",
          title: "Metode Pembayaran Kosong",
          text: "silahkan pilih metode pembayaran!",
          showConfirmButton: false,
          timer: 1500,
        });
      } else {
        Swal.fire({
          title: "Yakin untuk mengirim?",
          text: "Pastikan data form sudah benar!",
          icon: "warning",
          showCancelButton: true,
          confirmButtonColor: "#3085d6",
          cancelButtonColor: "#d33",
          confirmButtonText: "Yakin",
        }).then((result) => {
          if (result.isConfirmed) {
            const formData = new FormData();
            formData.append("metode_pembayaran", this.metodePembayaran);
            formData.append("no_hp", this.noTujuan);
            formData.append("produk", this.idProduct);
            formData.append("operatorProduk", this.idOperator.product_id);
            formData.append("cus_id", this.user.id);

            axios({
              method: "post",
              url:
                this.ipAdress +
                "api/transaksi/tb?username=" +
                this.user.username,
              data: formData,
              headers: { "Content-Type": "multipart/form-data" },
            })
              .then(function (response) {
                //handle success
                // console.log(response);
                if (response.data.status == "failed") {
                  Swal.fire({
                    icon: "error",
                    title: response.data.message,
                    text: response.data.keterangan,
                    showConfirmButton: true,
                    timer: 1500,
                  }).then(function () {
                    window.location = "/customer/data-transaksi";
                  });
                } else if (response.data == "") {
                  Swal.fire({
                    icon: "success",
                    title: "Diproses",
                    text: "Transaksi Diproses",
                    showConfirmButton: true,
                    timer: 1500,
                  }).then(function () {
                    window.location = "/customer/data-transaksi";
                  });
                } else {
                  Swal.fire({
                    icon: "success",
                    title: response.data.message,
                    text: response.data.keterangan,
                    showConfirmButton: true,
                    timer: 1500,
                  }).then(function () {
                    window.location = "/customer/data-transaksi";
                  });
                }
              })
              .catch(function (response) {
                //handle error
                console.log(response);
              });
          }
        });
      }
    },
    loadProduct() {
      if (this.user.verified == "0") {
        this.metodePembayaran = "Dompet";
      }
      axios
        .get(
          this.ipAdress +
            "api/get-prabayar-produk/" +
            this.idOperator.product_id +
            "?username=" +
            this.user.username
        )
        .then(({ data }) => {
          this.dataLoadProduct = data.filter(function (el) {
            return el.status == "ACTIVE";
          });
          // console.log(data);
        });
    },

    numberFormat(x) {
      if (x) {
        return x.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".");
      } else {
        return (x = "0");
      }
    },
    checkRekening: function () {
      this.noTujuan = this.noTujuan.replace(/[^0-9]/g, "");
      let idBank = this.idOperator.product_id;
      let minDigit = this.idOperator.min_digit;
      let maxDigit = this.idOperator.max_digit;

      if (this.noTujuan.length < minDigit) {
        this.noTujuanValid.status = false;
        this.noTujuanValid.message =
          "No Rekening " + idBank + " minimal " + minDigit + " digit";
      } else if (this.noTujuan.length > maxDigit) {
        this.noTujuanValid.status = false;
        this.noTujuanValid.message =
          "No Rekening " + idBank + " maximal " + maxDigit + " digit";
      } else {
        this.noTujuanValid.status = true;
      }
    },
  },
  computed: {
    orderedDataJenisBank: function () {
      return _.orderBy(this.dataJenisBank, "product_name", "asc");
    },
  },
  mounted() {},
};
</script>

<style>
html,
body {
  text-align: center;
}

body {
  background-color: #ffffff;
}

.rounded-2 {
  border-radius: 8px;
}

.rounded-5 {
  border-radius: 10px;
}

.rounded-10 {
  border-radius: 20px;
}

#navbottom {
  border-radius: 30px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}

.bg-biru {
  background-color: #1583dd;
}

.content {
  margin-top: 100px;
  margin-bottom: 130px;
}

.bg-putih {
  background-color: #ffffff;
}
#logo-operator {
  width: 30%;
}
</style>